<template>
  <div class="sendHouse" ref="sendHouse">
    <navigation-bar  @onLeftClick="goBack" pageName="发布房源" />
    <van-tabs v-model="active" :border="false" title-active-color="#17B7BD" line-width="50%" color="#17B7BD" :swipeable="true">
      <van-tab title="房源">
        <van-cell-group>
          <van-field required label="发布类型" input-align="right" style="padding-left: 0.42667rem;">
            <van-radio-group v-model="isRent" slot="input" class="houseType">
              <van-radio name="0" checked-color="#17B7BD">出售</van-radio>
              <van-radio name="1" checked-color="#17B7BD">出租</van-radio>
            </van-radio-group>
          </van-field>
          <van-field :value="selectedArea ? selectedProvince.NAME + selectedCity.NAME + selectedArea.NAME : ''" required label="所在地区" placeholder="请选择地区" input-align="right" style="padding-left: 0.42667rem;" disabled @click="showAreaSheet"/>
          <van-field v-model="houseArea" required label="房源小区" placeholder="请输入您的小区" input-align="right" style="padding-left: 0.42667rem;"/>
          <van-field label="楼栋门牌" input-align="right">
            <div class="right-item" slot="input">
              <div class="part">
                <input type="tel" placeholder="选填" @keyup="building = building.replace(/\D/g,'')" v-model="building">
                <div>栋(号)</div>
              </div>
              <div class="part">
                <input type="tel" placeholder="选填" @keyup="unit = unit.replace(/\D/g,'')" v-model="unit">
                <div>单元</div>
              </div>
              <div class="part">
                <input type="tel" placeholder="选填" @keyup="room = room.replace(/\D/g,'')" v-model="room">
                <div>室</div>
              </div>
            </div>
          </van-field>
          <van-field v-model="acreage" required label="面积" placeholder="请输入面积" input-align="right" style="padding-left: 0.42667rem;" type="tel" @input ="acreage = (acreage.match(/\d+(\.\d{0,2})?/)||[''])[0]">
            <div slot="button">平米</div>
          </van-field>
          <van-field label="户型" input-align="right">
            <div class="right-item" slot="input">
              <div class="part">
                <input type="tel" placeholder="选填" @keyup="roomNum = roomNum.replace(/\D/g,'')" v-model="roomNum">
                <div>室</div>
              </div>
              <div class="part">
                <input type="tel" placeholder="选填" @keyup="officeNum = officeNum.replace(/\D/g,'')" v-model="officeNum">
                <div>厅</div>
              </div>
              <div class="part">
                <input type="tel" placeholder="选填" @keyup="toiletNum = toiletNum.replace(/\D/g,'')" v-model="toiletNum">
                <div>卫</div>
              </div>
            </div>
          </van-field>
          <van-field label="楼层" input-align="right">
            <div class="right-item" slot="input">
              <div class="part">
                <input type="tel" placeholder="选填" @keyup="floor = floor.replace(/\D/g,'')" style="text-align: center" v-model="floor">
                <span>层/共</span>
              </div>
              <div class="part">
                <input type="tel" placeholder="选填" @keyup="floorNum = floorNum.replace(/\D/g,'')" style="text-align: center" v-model="floorNum">
              </div>
            </div>
          </van-field>
          <van-field label="房屋类型" input-align="right">
            <div slot="input">
              <van-dropdown-menu active-color="#17B7BD">
                <van-dropdown-item :options="houseTypeList" v-model="houseType"/>
              </van-dropdown-menu>
            </div>
          </van-field>
          <van-field label="装修" input-align="right">
            <div slot="input">
              <van-dropdown-menu active-color="#17B7BD">
                <van-dropdown-item :options="decorationList" v-model="decoration"/>
              </van-dropdown-menu>
            </div>
          </van-field>
          <van-field label="朝向" input-align="right">
            <div slot="input">
              <van-dropdown-menu active-color="#17B7BD">
                <van-dropdown-item :options="directionList" v-model="direction"/>
              </van-dropdown-menu>
            </div>
          </van-field>
          <van-field v-model="houseMoney" type="tel" id="houseTotalSale" @input="houseMoney =(houseMoney.match(/\d+(\.\d{0,2})?/)||[''])[0]" required label="售价" placeholder="请输入您的房价" input-align="right" style="padding-left: 0.42667rem;" v-show="isRent == '0'">
            <div slot="button">万</div>
          </van-field>
          <van-field required label="出租方式" input-align="right" style="padding-left: 0.42667rem;" v-show="isRent == '1'">
            <van-radio-group v-model="rentType" slot="input" class="houseType" checked-color="#17B7BD">
              <van-radio name="0" checked-color="#17B7BD">整套出租</van-radio>
              <van-radio name="1" checked-color="#17B7BD">单间出租</van-radio>
            </van-radio-group>
          </van-field>
          <van-field label="出租间" input-align="right" v-show="rentType == '1'">
            <div class="right-item" slot="input">
              <div class="part">
                <input type="tel" @keyup="rentAcreage=(rentAcreage.match(/\d+(\.\d{0,2})?/)||[''])[0]" placeholder="出租间面积" style="width: 2.33333rem;" v-model="rentAcreage">
                <div>M²</div>
              </div>
              <div class="part">
                <van-dropdown-menu active-color="#17B7BD">
                  <van-dropdown-item :options="siteList" v-model="site"/>
                  <van-dropdown-item :options="sexList" v-model="sex"/>
                </van-dropdown-menu>
              </div>
            </div>
          </van-field>
          <van-field required label="租金" input-align="right" style="padding-left: 0.42667rem;" v-show="isRent == '1'">
            <div class="right-item" slot="input">
              <div class="part">
                <input type="tel" @keyup="rentMoney=(rentMoney.match(/\d+(\.\d{0,2})?/)||[''])[0]" placeholder="请输入租金" style="width: 2.33333rem;" v-model="rentMoney">
                <div>元/月</div>
              </div>
              <div class="part">
                <van-dropdown-menu active-color="#17B7BD">
                  <van-dropdown-item :options="payTypeList" v-model="payType"/>
                </van-dropdown-menu>
              </div>
            </div>
          </van-field>
          <div v-show="isRent == '1'" class="house-equit">
            <div class="left">房屋设施</div>
            <div class="right">
              <van-checkbox-group v-model="selectedHouseEquit">
                <van-checkbox shape="square" checked-color="#17B7BD" v-for="(item,index) in houseEquipList" :key="index" :name="item">
                  {{item.NAME}}
                </van-checkbox>
              </van-checkbox-group>
            </div>
          </div>
          <div class="block"></div>
          <van-field v-model="houseTitle" required label="标题" placeholder="请输入您的标题" input-align="right" style="padding-left: 0.42667rem;"/>
          <van-field v-model="contact" required label="联系人" placeholder="请输入联系人姓名" input-align="right" style="padding-left: 0.42667rem;"/>
          <van-field v-model="contactPhone" required label="联系方式" :border="false" placeholder="请输入联系方式" input-align="right" style="padding-left: 0.42667rem;"/>
        </van-cell-group>
        <div class="bottom">
          <div class="detail">
            <div class="title">详细信息</div>
            <textarea name="" id="" placeholder="请输入详细信息" v-model="detail" maxlength="100"></textarea>
          </div>
          <div class="photo_box">
            <div class="title">
              <span class="add">房源图</span>
              <span>(*至多上传4张图片)</span>
            </div>
            <div class="image-box">
              <add-image v-on:imageAddAction="showAddSheetControll" v-on:imageMapChange="upDateImageData" :is-upload="isUpload" v-on:changeUploadStatus="changeStatus" ref="addImageComponent" :select-pictures="defaultImg"></add-image>
            </div>
          </div>
        </div>
        <div class="agreement">
          <van-checkbox v-model="isAgree" shape="square" checked-color="#17B7BD">
            <span @click="goAgreement">请阅读《发布房源》协议</span>
          </van-checkbox>
        </div>
        <div class="confirm">
          <div @click="clickRight(0)">确认发布</div>
        </div>
        <div style="width: 100%; background: #ffffff;" :style="{height: bottomHeight}"></div>
      </van-tab>
      <van-tab title="车位">
        <van-cell-group>
          <van-field required label="发布类型" input-align="right" style="padding-left: 0.42667rem;">
            <van-radio-group v-model="isRent" slot="input" class="houseType" checked-color="#17B7BD">
              <van-radio name="0" checked-color="#17B7BD">出售</van-radio>
              <van-radio name="1" checked-color="#17B7BD">出租</van-radio>
            </van-radio-group>
          </van-field>
          <van-field :value="selectedArea ? selectedProvince.NAME + selectedCity.NAME + selectedArea.NAME : ''" required label="所在地区" placeholder="请选择地区" input-align="right" style="padding-left: 0.42667rem;" disabled @click="showAreaSheet"/>
          <van-field v-model="houseArea" required label="小区" placeholder="请输入您的小区" input-align="right" style="padding-left: 0.42667rem;"/>
          <van-field v-model="acreage" required label="面积" placeholder="请输入面积" input-align="right" style="padding-left: 0.42667rem;" type="tel" @input="acreage=(acreage.match(/\d+(\.\d{0,2})?/)||[''])[0]">
            <div slot="button">平米</div>
          </van-field>
          <van-field v-model="houseMoney" required label="售价" placeholder="请输入您的房价" input-align="right" style="padding-left: 0.42667rem;" v-show="isRent == '0'" type="tel" @input="houseMoney=(houseMoney.match(/\d+(\.\d{0,2})?/)||[''])[0]">
            <div slot="button">万</div>
          </van-field>
          <van-field required label="租金" input-align="right" style="padding-left: 0.42667rem;" v-show="isRent == '1'">
            <div class="right-item" slot="input">
              <div class="part">
                <input type="tel" @keyup="rentMoney=(rentMoney.match(/\d+(\.\d{0,2})?/)||[''])[0]" placeholder="请输入租金" style="width: 2.33333rem;" v-model="rentMoney">
                <div>元/月</div>
              </div>
              <div class="part">
                <van-dropdown-menu active-color="#17B7BD">
                  <van-dropdown-item :options="payTypeList" v-model="payType"/>
                </van-dropdown-menu>
              </div>
            </div>
          </van-field>
          <van-field v-model="carNumber" required label="车位号" placeholder="请输入您的车位号" input-align="right" style="padding-left: 0.42667rem;"/>
          <div class="block"></div>
          <van-field v-model="houseTitle" required label="标题" placeholder="请输入您的标题" input-align="right" style="padding-left: 0.42667rem;"/>
          <van-field v-model="contact" required label="联系人" placeholder="请输入联系人姓名" input-align="right" style="padding-left: 0.42667rem;"/>
          <van-field v-model="contactPhone" required label="联系方式" placeholder="请输入联系方式" input-align="right" style="padding-left: 0.42667rem;"/>
        </van-cell-group>
        <div class="bottom">
          <div class="detail">
            <div class="title">详细信息</div>
            <textarea name="" id="" placeholder="请输入详细信息" v-model="detail" maxlength="100"></textarea>
          </div>
          <div class="photo_box">
            <div class="title">
              <span class="add">车位图</span>
              <span>(*至多上传4张图片)</span>
            </div>
            <div class="image-box">
              <add-image v-on:imageAddAction="showAddSheetControll" v-on:imageMapChange="upDateImageData" v-on:changeUploadStatus="changeStatus" :is-upload="isUpload" ref="addImageComponent" :select-pictures="defaultImg"></add-image>
            </div>
          </div>
        </div>
        <div class="agreement">
          <van-checkbox v-model="isAgree" shape="square" checked-color="#17B7BD">
            <span @click="goAgreement">请阅读《发布房源》协议</span>
          </van-checkbox>
        </div>
        <div class="confirm">
          <div @click="clickRight(1)">确认发布</div>
        </div>
        <div style="width: 100%; background: #ffffff;" :style="{height: bottomHeight}"></div>
      </van-tab>
    </van-tabs> 
    <van-popup v-model="isShowAreaSheet" position="bottom">
      <div class="area-box">
        <div class="title">
          所在地区
          <div class="close" @click="isShowAreaSheet = false">
            <img :src="closeIcon" alt="">
          </div>
        </div>
        <div class="area-title">
          <div class="province" :class="activetab == 0 ? 'active' : ''" @click="clickProvince">省</div>
          <div class="city" :class="activetab == 1 ? 'active' : ''" @click="clickCity">市</div>
          <div class="area" :class="activetab == 2 ? 'active' : ''" @click="clickArea">区</div>
        </div>
        <div class="list">
          <div class="item" v-for="(item, index) in showSelectList" :key="index" @click="selectArea(item)">
            {{item.NAME}}
          </div>
        </div>
      </div>
    </van-popup>
    <van-action-sheet
          v-model="showAddSheet"
          :actions="actions"
          cancel-text="取消"
          :round="false"
          close-on-click-action
          @select="addSelect"/>  
  </div>
</template>

<script>
import { Toast, Dialog } from "vant";
import {
  getHouseTypeList,
  getDecorationList,
  getDirectionList,
  getPayTypeList,
  getHouseEquipList,
  getProvinceList,
  getCityList,
  getAreaList,
  sendHouse
} from "../../../services/houseRentalService";
import AddImageVue from "../../../components/addImage/AddImage.vue";
import leftIcon from "@/assets/images/common/nav_return_white.png";
import closeIcon from "@/assets/images/home/houseRental/close_black.png";
import NavigationBar from '@/common/currency/NavigationBar.vue';
export default {
  name: "sendHouse",
  data() {
    return {
      leftIcon: leftIcon,
      closeIcon: closeIcon,
      // 房源还是车位
      active: 0,
      showAddSheet: false,
      isShowAreaSheet: false,
      activetab: 0,
      actions: [{ name: "拍照" }, { name: "从手机相册选择" }],
      houseTypeList: [],
      decorationList: [],
      directionList: [],
      payTypeList: [],
      houseEquipList: [],
      sexList: [
        { value: 0, text: "女", code: '0' },
        { value: 1, text: "男", code: '1' }
      ],
      siteList: [
        //主次卧
        { value: 0, text: "次卧", code: '0' },
        { value: 1, text: "主卧", code: '1' }
      ],
      // 省市区列表
      showSelectList: [],
      // 接口参数
      isRent: "0", // 是否是出租，默认不是
      selectedProvince: null, // 省
      selectedCity: null, // 市
      selectedArea: null, // 区
      houseArea: "", // 房源小区
      building: "", // 楼栋
      unit: "", // 单元
      room: "", // 室
      acreage: "", // 面积
      roomNum: "", //几室
      officeNum: "", // 几厅
      toiletNum: "", // 几卫
      floor: "", //几层
      floorNum: "", // 共几层
      houseType: 0, // 房屋类型
      decoration: 0, // 装修
      direction: 0, // 朝向
      houseMoney: "", // 房价
      rentType: "0", //出租方式
      rentAcreage: "", // 出租间面积
      site: 0, // 主卧次卧
      sex: 0, // 性别
      rentMoney: "", // 租金
      payType: 0, // 支付方式
      selectedHouseEquit: [], // 房屋设施
      houseTitle: "", // 标题
      contact: "", // 联系人
      contactPhone: "", //联系方式
      detail: "", // 详细信息
      imgList: [], // 图片列表
      carNumber: "", // 车位号
      isAgree: true,
      bottomHeight: "0px",
      defaultImg: [],
      isUpload: false
    };
  },
  mounted () {
    /**
     * 页面滚动监测
     **/   
    document.querySelector(".van-tabs__content").addEventListener(
      "scroll",
      e => {
        this.resetDrawCursor();
      },
      { passive: false }
    );
  },
  activated () {
    window.addEventListener("native.keyboardshow", 
      this.keyboardshow
    );
    window.addEventListener("native.keyboardhide", 
      this.keyboardhide
    );
    if(this.$store.state.houseRental.reload) {
      this.reset();
      this.$store.dispatch('setReload', false);
      this.getHouseTypeList();
      this.getDecorationList();
      this.getDirectionList();
      this.getPayTypeList();
      this.getHouseEquipList();
    }
  },
  beforeRouteLeave(to, from, next) {
    console.log("sendHouse--------")
    window.removeEventListener("native.keyboardshow", 
      this.keyboardshow
    );
    window.removeEventListener("native.keyboardhide",
      this.keyboardhide
    );
    next();
  },
  components: {
    "add-image": AddImageVue,
    NavigationBar
  },
  methods: {
    //重新绘制光标,只针对iOS
    resetDrawCursor() {
      if (this.$devicePlatform != 'ios') {
        return;
      }

      var node = document.activeElement; //当前focus的dom元素
      if (node) {
        if (node.nodeName == "TEXTAREA" || node.nodeName == 'INPUT') { //如果是input或textarea
            if (node.style.textShadow === '') {
                node.style.textShadow = 'rgba(0,0,0,0) 0 0 0'; //改变某个不可见样式，触发dom重绘
            } else {
                node.style.textShadow = '';
            }
        }
      }
    },
    /**
     * 键盘弹出事件
     */
    keyboardshow(e) {
      this.bottomHeight = (e.keyboardHeight + 50) + "px"
      setTimeout(() => {
        document.activeElement.scrollIntoView();
        this.resetDrawCursor();
      }, 100);
    },

    /**
     * 键盘隐藏事件
     */
    keyboardhide(e) {
      this.bottomHeight = "0px";
    },

    /**
     * 页面返回事件
     */
    goBack() {
      this.reset();
      this.$router.go(-1);
    },

    reset() {
      this.active = 0;
      this.showAddSheet = false;
      this.isShowAreaSheet = false;
      this.activetab = 0;
      this.houseTypeList = [];
      this.decorationList = [];
      this.directionList = [];
      this.payTypeList = [];
      this.houseEquipList = [];
      this.showSelectList = [];// 省市区列表
      // 接口参数
      this.isRent = "0"; // 是否是出租，默认不是
      this.selectedProvince = null; // 省
      this.selectedCity = null; // 市
      this.selectedArea = null; // 区
      this.houseArea = ""; // 房源小区
      this.building = ""; // 楼栋
      this.unit = ""; // 单元
      this.room = ""; // 室
      this.acreage = ""; // 面积
      this.roomNum = ""; //几室
      this.officeNum = ""; // 几厅
      this.toiletNum = ""; // 几卫
      this.floor = ""; //几层
      this.floorNum = ""; // 共几层
      this.houseType = 0; // 房屋类型
      this.decoration = 0; // 装修
      this.direction = 0; // 朝向
      this.houseMoney = ""; // 房价
      this.rentType = "0"; //出租方式
      this.rentAcreage = ""; // 出租间面积
      this.site = 0; // 主卧次卧
      this.sex = 0; // 性别
      this.rentMoney = ""; // 租金
      this.payType = 0; // 支付方式
      this.selectedHouseEquit = []; // 房屋设施
      this.houseTitle = ""; // 标题
      this.contact = ""; // 联系人
      this.contactPhone = ""; //联系方式
      this.detail = ""; // 详细信息
      this.imgList = []; // 图片列表
      this.carNumber = ""; // 车位号
      this.isAgree = true;
      this.defaultImg = [];
      this.isUpload = false;
    },

    /**
     * 获取房屋类型列表
     */
    getHouseTypeList() {
      var data = {
        table_name: "T_CODE_HOUSE_TYPE",
        code: "HOUSE_TYPE_CODE",
        name: "HOUSE_TYPE_NAME"
      };
      getHouseTypeList({
        data: JSON.stringify(data)
      }).then(
        result => {
          var arr = [];
          for (var i = 0; i < result.length; i++) {
            arr.push({ text: result[i].NAME, value: i, code: result[i].CODE });
          }
          this.houseTypeList = arr;
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 获取装修方式列表
     */
    getDecorationList() {
      var data = {
        table_name: "T_CODE_DECORATE_TYPE",
        code: "DECORATE_TYPE_CODE",
        name: "DECORATE_TYPE_NAME"
      };
      getDecorationList({
        data: JSON.stringify(data)
      }).then(
        result => {
          var arr = [];
          for (var i = 0; i < result.length; i++) {
            arr.push({ text: result[i].NAME, value: i, code: result[i].CODE });
          }
          this.decorationList = arr;
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 获取朝向列表
     */
    getDirectionList() {
      var data = {
        table_name: "T_CODE_ORIENTATION_TYPE",
        code: "ORIENTATION_TYPE_CODE",
        name: "ORIENTATION_TYPE_NAME"
      };
      getDirectionList({
        data: JSON.stringify(data)
      }).then(
        result => {
          var arr = [];
          for (var i = 0; i < result.length; i++) {
            arr.push({ text: result[i].NAME, value: i, code: result[i].CODE });
          }
          this.directionList = arr;
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 获取支付方式列表
     */
    getPayTypeList() {
      var data = {
        table_name: "T_CODE_RENTAL",
        code: " RENTAL_CODE",
        name: "RENTAL_NAME"
      };
      getPayTypeList({
        data: JSON.stringify(data)
      }).then(
        result => {
          var arr = [];
          for (var i = 0; i < result.length; i++) {
            arr.push({ text: result[i].NAME, value: i, code: result[i].CODE });
          }
          this.payTypeList = arr;
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 获取房屋设施列表
     */
    getHouseEquipList() {
      var data = {
        table_name: "T_CODE_RENT_EQUIP",
        code: " EQUIP_CODE",
        name: "EQUIP_NAME"
      };
      getHouseEquipList({
        data: JSON.stringify(data)
      }).then(
        result => {
          this.houseEquipList = result;
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 展示地区选择弹窗
     */
    showAreaSheet() {
      this.activetab = 0;
      this.isShowAreaSheet = true;
      this.getProvinceList();
    },

    /**
     * 省点击事件
     */
    clickProvince() {
      this.activetab = 0;
      this.getProvinceList();
    },

    /**
     * 市点击事件
     */
    clickCity() {
      if (!this.selectedProvince) {
        this.$toast("请先选择省份");
        return;
      }
      this.activetab = 1;
      this.getCityList();
    },

    /**
     * 区点击事件
     */
    clickArea() {
      if (!this.selectedCity) {
        this.$toast("请先选择城市");
        return;
      }
      this.activetab = 2;
      this.getAreaList();
    },

    /**
     * 获取省列表
     */
    getProvinceList() {
      var data = {
        table_name: "T_CODE_PROVINCE",
        code: "PROVINCE_CODE",
        name: "PROVINCE_NAME"
      };
      getProvinceList({
        data: JSON.stringify(data)
      }).then(
        result => {
          this.showSelectList = result;
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 获取市列表
     */
    getCityList() {
      var data = {
        table_name: "T_CODE_CITY",
        code: "CITY_CODE",
        name: "CITY_NAME",
        where_key: "PROVINCE_CODE",
        where_value: this.selectedProvince.CODE
      };
      getCityList({
        data: JSON.stringify(data)
      }).then(
        result => {
          this.showSelectList = result;
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 获取区列表
     */
    getAreaList() {
      var data = {
        table_name: "T_CODE_COUNTY",
        code: "COUNTY_CODE",
        name: "COUNTY_NAME",
        where_key: "CITY_CODE",
        where_value: this.selectedCity.CODE
      };
      getAreaList({
        data: JSON.stringify(data)
      }).then(
        result => {
          this.showSelectList = result;
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 省市区列表点击事件
     */
    selectArea(item) {
      if (this.activetab == 0) {
        this.activetab = 1;
        this.selectedProvince = item;
        this.getCityList();
      } else if (this.activetab == 1) {
        this.activetab = 2;
        this.selectedCity = item;
        this.getAreaList();
      } else if (this.activetab == 2) {
        this.selectedArea = item;
        this.isShowAreaSheet = false;
      }
    },

    send() {
      var data = '';
      if (this.sendStatus == 0) {
        // 房屋出租
        data = {
          CAR_LOCATION_FLAG: "1", //车位类型标记(0车位 ；1房源)
          PLC_LIST: this.getImgList(),
          USER_CODE: this.$userInfo.OWNER_CODE || "", //当前登录人代码
          HOUSE_FLAG: this.isRent, //类型标记(0出售；1出租)
          PROVINCE_CODE: this.selectedProvince.CODE || '', //省代码
          PROVINCE_NAME: this.selectedProvince.NAME || '', //省名称
          CITY_CODE: this.selectedCity.CODE || '', //市代码
          CITY_NAME: this.selectedCity.NAME || '', //市名称
          COUNTY_CODE: this.selectedArea.CODE, //区代码
          COUNTY_NAME: this.selectedArea.NAME, //区名称
          AREA: this.houseArea, //房源小区
          BUILD_NO: this.building === '' ? null : this.building, //栋号
          UNIT_NO: this.unit === '' ? null : this.unit, //单元号
          ROOM_NO: this.room === '' ? null : this.room , //室号
          ACREAGE: this.acreage, //面积
          ROOM_CNT: this.roomNum === '' ? null : this.roomNum, //户型_室
          HALL_CNT: this.officeNum === '' ? null : this.officeNum, //户型_厅
          TOILET_CNT: this.toiletNum === '' ? null : this.toiletNum, //户型_卫
          TOTAL_FLOOR_CNT: this.floorNum === '' ? null : this.floorNum, //楼层_总
          NOW_FLOOR_CNT: this.floor === '' ? null : this.floor, //楼层_当前
          HOUSE_TYPE_CODE: this.houseTypeList[this.houseType].code, //房屋类型代码
          DECORA_TYPE_CODE: this.decorationList[this.decoration].code, //装修类型代码
          FRONT_TYPE_CODE: this.decorationList[this.direction].code, //朝向类型代码
          LINKMAN_NAME: this.contact, //联系人姓名
          LINKMAN_PHONE: this.contactPhone, //  联系人电话
          CONTENT: this.detail, //详细内容
          PRICE: this.isRent == "0" ? this.houseMoney : this.rentMoney, //售价
          PRICE_HOUSE_SALE: this.isRent == "0" ? this.houseMoney : '', //售价-卖房
          PRICE_HOUSE_RENT: this.isRent == "1" ? this.rentMoney : '', //售价-租房
          PRICE_CAR_SALE: "", //售价-卖车
          PRICE_CAR_RENT: "", //售价-租车
          TITLE: this.houseTitle, //标题
          RENT_FLAG: this.isRent == "1" ? this.rentType * 1: null, //出租方式标记(0整间出租；1单间出租)
          MASTER_FLAG: this.isRent == "1" && this.rentType == '1' ? this.siteList[this.site].code : null, //主次卧(1主卧；0次卧)
          RENT_NUM: this.isRent == "1" && this.rentType == '1' ? this.rentAcreage : null, //出租间面积
          SEX: this.isRent == "1" && this.rentType == '1' ? this.sexList[this.sex].code : null, //性别 0女1男
          RENTAL_CODE: this.isRent == "1" ? this.payTypeList[this.payType].code : '', //租金支付方式代码
          EQUIPLIST: this.isRent == "1" ? this.getEquipRequestData() : '', //房屋设备,穿参如"EQUIPLIST": [{"EQUIP_CODE": "1","EQUIP_NAME": "水"},{"EQUIP_CODE": "2","EQUIP_NAME": "电" },{"EQUIP_CODE": "3","EQUIP_NAME": "煤气"}]
          OWNER_CODE: this.$userInfo.OWNER_CODE,
          AREA_CODE: this.$userInfo.AREA_CODE
        };
      } else {
        data = {
          CAR_LOCATION_FLAG: '0', //车位类型标记(0车位 ；1房源)
          PLC_LIST: this.getImgList(),
          USER_CODE: this.$userInfo.OWNER_CODE || "", //当前登录人代码
          HOUSE_FLAG: this.isRent, //类型标记(0出售；1出租)
          PROVINCE_CODE: this.selectedProvince.CODE || '', //省代码
          PROVINCE_NAME: this.selectedProvince.NAME || '', //省名称
          CITY_CODE: this.selectedCity.CODE || '', //市代码
          CITY_NAME: this.selectedCity.NAME || '', //市名称
          COUNTY_CODE: this.selectedArea.CODE, //区代码
          COUNTY_NAME: this.selectedArea.NAME, //区名称
          AREA: this.houseArea, //房源小区
          ACREAGE: this.acreage, //面积
          PRICE: this.isRent == "0" ? this.houseMoney : this.rentMoney, //售价
          PRICE_CAR_SALE: this.isRent == "0" ? this.houseMoney : '', //售价-卖车
          PRICE_CAR_RENT: this.isRent == "1" ? this.rentMoney : '', //售价-租车
          RENTAL_CODE: this.isRent == "1" ? this.payTypeList[this.payType].code : '', //租金支付方式代码
          PARK_NO: this.carNumber, //车位号
          TITLE: this.houseTitle, //标题
          LINKMAN_NAME: this.contact, //联系人姓名
          LINKMAN_PHONE: this.contactPhone, //  联系人电话
          CONTENT: this.detail, //详细内容
          MASTER_FLAG: '1', //主次卧(1主卧；0次卧)
          OWNER_CODE: this.$userInfo.OWNER_CODE,
          AREA_CODE: this.$userInfo.AREA_CODE
        };
      }
      sendHouse({
        data: JSON.stringify(data)
      }).then(result => {
        this.$toast("发送成功");
        this.$store.dispatch("setReloadMine", true);
        this.$store.dispatch("setMineActive", this.active * 1);
        this.$router.replace({ name: "myHouse"})
      }, error => {
        this.$toast(error)
      })
    },

    clickRight(status) {
      this.sendStatus = status;
      if (!this.checkRequestData(status)) {
        return;
      } else {
        this.isUpload = true;
      }
    },

    changeStatus() {
      this.isUpload = false;
    },

    /**
     * 获取房屋设施列表
     */
    getEquipRequestData() {
      var arr = []
      this.selectedHouseEquit.forEach(item => {
        arr.push({"EQUIP_CODE": item.CODE,"EQUIP_NAME": item.NAME})
      })
      return arr;
    },

    /**
     * 获取图片数据
     */
    getImgList() {
      var arr = []
      this.imgList.forEach(item => {
        arr.push({"PIC_URL": item})
      })
      return arr;
    },

    /**
     * 检查数据
     */
    checkRequestData(status) {
      if(!this.isAgree) {
        this.$toast("请阅读并同意《发布房源》协议");
        return false;
      }
      if (this.$userInfo.OWNER_CODE == "" || this.$userInfo.AREA_CODE == ""
      ) {
        //用户未登录
        this.$toast("请登录");
        return false;
      } else if (!this.selectedArea) {
        this.$toast("请选择地区");
        return false;
      } else if (this.houseArea == "") {
        this.$toast("请输入小区");
        return false;
      } else if (!this.acreage) {
        this.$toast("请输入面积");
        return false;
      } else if (this.isRent == "1" && !this.rentMoney) {
        this.$toast("请输入售价/租金");
        return false;
      } else if (this.isRent == "0" && !this.houseMoney) {
        this.$toast("请输入售价/租金");
        return false;
      } else if (this.houseTitle == "") {
        this.$toast("请输入标题");
        return false;
      } else if (this.contact == "") {
        this.$toast("请输入联系人");
        return false;
      } else if (this.contactPhone == "") {
        this.$toast("请输入联系方式");
        return false;
      } else if (status == 1 && this.carNumber == "") {
        //发布车位车位号必填
        this.$toast("请输入车位号");
        return false;
      } else if (!(/^1[0-9]{10}$/.test(this.contactPhone))) {
        this.$toast("请输入正确的手机号码");
        return false;
      }
      return true;
    },

    /**
     * 跳转到发布房源协议页面
     */
    goAgreement() {
      this.$router.push({
        name: "agreement"
      });
    },

    /**
     * 子组件更新已选图片集合监听函数
     **/
    upDateImageData(newImageData) {
      this.imgList = newImageData;
      this.send();
    },

    /**
     * addImage组件添加照片时的监听函数父组件用来选择哪种图片获取方式(拍照或相册上传)
     **/
    showAddSheetControll() {
      this.showAddSheet = !this.showAddSheet;
    },

    /**
     * 选择当前图片来源方式(拍照或本地上传)
     * @param
     * item      Object   当前选择的方式对象
     * index     Number   当前选择的索引
     **/
    addSelect(item, index) {
      this.getPictureData(index);
    },

    /**
     * 调用摄像头或者本地相册进行图片上传
     * @param
     **/
    getPictureData(selectIndex) {
      selectIndex
        ? this.$refs.addImageComponent.getPicture()
        : this.$refs.addImageComponent.useCamera();
    }
  }
};
</script>

